@use 'sass:math';

.flex-table {
  --base-font-family: 'Graphik LC', Arial, sans-serif;
  --comp-font-family: 'Graphik Compact', 'Graphik LC', Arial, sans-serif;
  --anabar__text-RGB: 0, 20, 60;
  --anabar__data-text-RGB: 0, 0, 0;
  --anabar__link-RGB: 0, 20, 60;
  --anabar__hover-RGB: 0, 90, 255;
  --anabar__surface-RGB: 245, 242, 255;
  --anabar__background-RGB: 255, 255, 255;
  --anabar__main-RGB: var(--anabar__hover-RGB);

  --base-z-index: 1;
  --body-z-index: calc(var(--base-z-index) + 1);
  --header-z-index: calc(var(--body-z-index) + 1);
  --photo-cell-z-index: calc(var(--header-z-index) + 1);
  --tooltip-z-index: calc(var(--header-z-index) - 1);

  box-sizing: border-box;
  font-family: var(--base-font-family);
  font-size: 20px;
  line-height: 1.1;
  color: RGB(var(--anabar__text-RGB));
  will-change: font-size;

  * {
    box-sizing: border-box;
  }

  &.prevent-hover {
    pointer-events: none;
  }

  sup {
    font-size: 75%;
    line-height: 0;
    position: relative;
    vertical-align: baseline;
    top: -0.5em;
  }

  input {
    overflow: visible;
    font-family: inherit;
    font-size: 100%;
    line-height: 1.15;
    margin: 0;
  }

  a {
    color: RGB(var(--anabar__link-RGB));
    border-bottom: 1px solid rgba(var(--anabar__link-RGB), 0.2);
    text-decoration: none;
    transition: background-color 0.3s ease, color 0.3s ease,
      border-bottom-color 0.3s ease;

    &:hover {
      color: RGB(var(--anabar__hover-RGB));
      border-bottom-color: rgba(var(--anabar__hover-RGB), 0.2);
      transition: background-color 0s ease, color 0s ease,
        border-bottom-color 0s ease;
      text-decoration: none;
    }
  }
}

.flex-table_locked {
  pointer-events: none;
  opacity: 0.75;
  filter: grayscale(80%);
  cursor: progress;
}

.flex-table__bar {
  display: flex;
  align-items: flex-start;
  line-height: 1.4;
}

.flex-table__hidden-cols {
  font-size: 15px;
  margin-bottom: 18px;
}

.flex-table__hidden-item {
  white-space: nowrap;
  cursor: pointer;
  margin: 0;

  &:hover {
    color: rgb(var(--anabar__main-RGB));
  }

  &--all {
    margin-left: 1em;

    &:not(:hover) {
      color: rgba(var(--anabar__text-RGB), 0.3);
    }
  }
}

.flex-table__settings {
  display: flex;
  justify-content: flex-end;
  margin-left: auto;
  flex-shrink: 0;

  label {
    margin: 0;
  }
}

.flex-table-range {
  $track-color: rgba(0, 20, 60, 1);
  $thumb-color: rgba(0, 20, 60, 1);

  $thumb-radius: 5px !default;
  $thumb-height: 10px;
  $thumb-width: 10px;
  $thumb-shadow-size: 0 !default;
  $thumb-shadow-blur: 0 !default;
  $thumb-shadow-color: rgba(0, 0, 0, 0.2) !default;
  $thumb-border-width: 0 !default;
  $thumb-border-color: #eceff1 !default;

  $track-width: 100% !default;
  $track-height: 3px !default;
  $track-shadow-size: 0 !default;
  $track-shadow-blur: 0 !default;
  $track-shadow-color: rgba(0, 0, 0, 0.2) !default;
  $track-border-width: 0 !default;
  $track-border-color: #cfd8dc !default;

  $track-radius: 5px !default;
  $contrast: 5% !default;

  $ie-bottom-track-color: darken($track-color, $contrast) !default;

  @mixin shadow($shadow-size, $shadow-blur, $shadow-color) {
    box-shadow: $shadow-size $shadow-size $shadow-blur $shadow-color,
      0 0 $shadow-size lighten($shadow-color, 5%);
  }

  @mixin track {
    cursor: default;
    height: $track-height;
    width: $track-width;
  }

  @mixin thumb {
    @include shadow(
      $thumb-shadow-size,
      $thumb-shadow-blur,
      $thumb-shadow-color
    );
    background: $thumb-color;
    border: $thumb-border-width solid $thumb-border-color;
    border-radius: $thumb-radius;
    box-sizing: border-box;
    cursor: pointer;
    height: $thumb-height;
    width: $thumb-width;
  }

  position: relative;
  width: $track-width;
  margin: math.div($thumb-height, 2) 0;
  background: transparent;
  -webkit-appearance: none;

  &::-moz-focus-outer {
    border: 0;
  }

  &:focus {
    outline: 0;
    box-shadow: none;

    &::-webkit-slider-runnable-track {
      background: lighten($track-color, $contrast);
    }

    &::-ms-fill-lower {
      background: $track-color;
    }

    &::-ms-fill-upper {
      background: lighten($track-color, $contrast);
    }
  }

  &::-webkit-slider-runnable-track {
    @include track;
    @include shadow(
      $track-shadow-size,
      $track-shadow-blur,
      $track-shadow-color
    );
    background: $track-color;
    border: $track-border-width solid $track-border-color;
    border-radius: $track-radius;
  }

  &::-webkit-slider-thumb {
    @include thumb;
    -webkit-appearance: none;
    margin-top: (
      math.div((-$track-border-width * 2 + $track-height), 2) -
        math.div($thumb-height, 2)
    );
  }

  &::-moz-range-track {
    @include shadow(
      $track-shadow-size,
      $track-shadow-blur,
      $track-shadow-color
    );
    @include track;
    background: $track-color;
    border: $track-border-width solid $track-border-color;
    border-radius: $track-radius;
    height: math.div($track-height, 2);
  }

  &::-moz-range-thumb {
    @include thumb;
  }

  &::-ms-track {
    @include track;
    background: transparent;
    border-color: transparent;
    border-width: math.div($thumb-height, 2) 0;
    color: transparent;
  }

  &::-ms-fill-lower {
    @include shadow(
      $track-shadow-size,
      $track-shadow-blur,
      $track-shadow-color
    );
    background: $ie-bottom-track-color;
    border: $track-border-width solid $track-border-color;
    border-radius: ($track-radius * 2);
  }

  &::-ms-fill-upper {
    @include shadow(
      $track-shadow-size,
      $track-shadow-blur,
      $track-shadow-color
    );
    background: $track-color;
    border: $track-border-width solid $track-border-color;
    border-radius: ($track-radius * 2);
  }

  &::-ms-thumb {
    @include thumb;
    margin-top: math.div($track-height, 4);
  }

  &:disabled {
    &::-webkit-slider-thumb,
    &::-moz-range-thumb,
    &::-ms-thumb,
    &::-webkit-slider-runnable-track,
    &::-ms-fill-lower,
    &::-ms-fill-upper {
      cursor: not-allowed;
    }
  }
}

.flex-table__setting {
  display: flex;
  font-size: 14px;
  margin-right: 20px;

  &::last-child {
    margin-right: 10px;
  }

  label {
    display: inline-block;
    margin-right: 8px;
    font-weight: 600;
  }

  input[type='range'] {
    padding: 0;
    border: none;
  }
}

.flex-table__table {
  .th {
    position: sticky;
    top: 0;
    background-color: rgba(var(--anabar__surface-RGB), 1);
    z-index: var(--header-z-index);
  }

  .tr {
    display: flex;
  }

  .td {
    width: 100%;

    &.align-right {
      text-align: right;
    }

    &.number {
      font-variant-numeric: tabular-nums;
    }

    &.number,
    &.date {
      .cell {
        white-space: nowrap;
      }
    }
  }
}

.flex-table__body {
  z-index: var(--body-z-index);
}

.flex-table__body .tr {
  display: flex;
  height: var(--table-row-height);
  border-bottom: 1px solid rgba(var(--anabar__text-RGB), 0.1);
  will-change: height;
}

.flex-table__body .td {
  position: relative;
  overflow: hidden;
  --cell-color: rgb(var(--anabar__background-RGB));
  color: RGB(var(--anabar__data-text-RGB));

  &:not(:last-child) {
    border-right: 1px solid rgba(var(--anabar__text-RGB), 0.1);
  }

  &:after {
    content: '';
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    width: 0.5em;
    background-image: linear-gradient(
      to right,
      rgba(var(--anabar__background-RGB), 0) 0%,
      rgba(var(--anabar__background-RGB), 0.8) 80%
    );
  }

  .cell {
    position: absolute;
    max-width: 200%;
    width: auto;
    padding: 0.3em 0.2em 0.5em 0.2em;
    border: 1px solid rgba(var(--anabar__background-RGB), 1);
    border-radius: 0.3em;
  }

  &.hover-show {
    &:hover {
      overflow: visible;

      &.photo {
        .cell {
          z-index: var(--photo-cell-z-index);
        }
      }

      .cell {
        background-color: rgba(var(--anabar__background-RGB), 1);
        border: 1px solid rgba(var(--anabar__text-RGB), 0.1);
        z-index: var(--tooltip-z-index);
        pointer-events: none;
        padding: calc(0.3em + 1px) 0.2em 0.5em calc(0.2em + 1px);
        margin: -1px;

        & .photo__preview {
          display: none;
        }

        & .photo__source {
          display: initial;
        }

        &.left {
          left: initial;
          right: 0;
        }

        &.right {
          left: 0;
          right: initial;
        }
      }

      &.align-right {
        .cell {
          padding: calc(0.3em + 1px) calc(0.2em + 1px) 0.5em 0.2em;
        }
      }

      a {
        pointer-events: auto;
      }
    }

    &.text,
    &.category,
    &.markdown {
      .cell {
        width: 180%;

        &.limit-width {
          max-width: 100%;
        }
      }
    }
  }

  &.align-right {
    .cell {
      right: 0;
    }
    &:after {
      right: auto;
      left: 0;
      background-image: linear-gradient(
        to left,
        rgba(var(--anabar__background-RGB), 0) 0%,
        rgba(var(--anabar__background-RGB), 0.8) 80%
      );
    }
  }

  &.photo {
    display: flex;
    align-items: center;

    .cell {
      width: 100%;
      left: -5%;

      img {
        width: 90%;
        margin-left: calc(5% - 1px);
      }

      .photo__preview {
        display: initial;
      }

      .photo__source {
        &_show_on_hover {
          display: none;
        }
      }
    }
  }

  &.graphic {
    .cell {
      width: 100%;
      height: 100%;

      img {
        width: 100%;
        height: 100%;
        max-height: 3em;
      }
    }
  }

  &.text,
  &.category {
    .cell {
      font-family: var(--base-font-family);
    }

    .flex-table__category_ul {
      list-style: none;
      margin: 0;
      padding: 0;
    }

    .flex-table__category_li {
      margin: 0;
      padding: 0;
      background: none;

      &:before,
      &:after {
        content: none;
      }
    }
  }

  &--bar-graph {
    &::before {
      position: absolute;
      top: 0;
      right: 0;
      left: auto;
      bottom: 0;
      display: block;
      content: '';
      width: var(--bar-size);
      min-width: 1px;
      background-color: #b8ccff;
    }
  }

  &--color {
    background-color: rgb(var(--cell-color));
  }

  &--graph {
    .cell {
      border: 0;
    }

    &.hover-show {
      &:hover {
        overflow: hidden;

        .cell {
          background-color: transparent;
          border: 0;
        }
      }
    }
  }
}

.flex-table__info {
  display: flex;
  align-items: center;
  justify-content: space-between;
  height: 35px;
  padding: 7px 0;
  border-bottom: 1px solid rgba(var(--anabar__text-RGB), 0.1);
}

.flex-table__info-item {
  font-size: 15px;
  line-height: 15px;
  padding-left: 5px;
}

.flex-table__head-col {
  position: relative;
  overflow: hidden;
  flex-grow: 0;

  &:not(:last-child) {
    border-right: 1px solid rgba(var(--anabar__text-RGB), 0.1);
  }

  &:after {
    content: '';
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    width: 1em;
    background-image: linear-gradient(
      to right,
      rgba(var(--anabar__surface-RGB), 0) 0%,
      rgba(var(--anabar__surface-RGB), 1) 80%
    );
  }

  &.align-right {
    .cell {
      right: 0;

      span {
        float: right;
        padding-bottom: 0.4em;
      }
    }
    &:after {
      right: auto;
      left: 0;
      background-image: linear-gradient(
        to left,
        rgba(var(--anabar__surface-RGB), 0),
        rgba(var(--anabar__surface-RGB), 1)
      );
    }
  }

  .filtered & {
    .flex-table__head-inner {
      padding-bottom: 2.2em;
    }
  }

  .cell {
    width: 100%;
    padding: 0.2em;
  }

  &:hover {
    .flex-table__filter {
      .flex-table__filter-inner {
        .flex-table__filter-text {
          opacity: 0 !important;
        }
      }
    }
  }
}

.flex-table__head-inner {
  position: relative;
  overflow: hidden;
  padding-top: 1em;
  height: 100%;
}

.flex-table__actions {
  position: absolute;
  top: 0;
  left: 0.2em;
  z-index: 1;
  display: flex;
  align-items: flex-end;

  .td.align-right & {
    right: 0.2em;
    left: auto;
  }

  &.hidden {
    transition: 0.3s opacity ease;
    opacity: 0;
    pointer-events: none;

    &.sorted,
    &.filtered,
    &:focus-within,
    .flex-table__head-col:hover & {
      opacity: 1;
      pointer-events: all;
    }

    .onblur & {
      transition-duration: 0s;
      opacity: 0;
    }
  }

  &.hidden.sorted {
    display: flex !important;

    .flex-table__action-btn:not(.flex-table__action-btn--sort) {
      transition: 0.3s opacity ease;
      opacity: 0;
      pointer-events: none;
    }
  }

  &.hidden.filtered {
    .flex-table__action-btn:not(.flex-table__action-btn--filter) {
      transition: 0.3s opacity ease;
      opacity: 0;
      pointer-events: none;
    }

    &.sorted {
      .flex-table__action-btn--sort {
        opacity: 1;
      }
    }
  }
}

.flex-table__action-btn {
  background: none !important;
  border: 0;
  padding: 0 0.1em;
  cursor: pointer;
  font-size: 1em;
  color: inherit;
  height: auto;

  &:hover {
    color: rgb(0, 90, 255);
  }

  svg {
    display: block;
    fill: currentColor;
    width: 1em;
    height: 1em;
  }

  &--filter {
    .column-filtered & {
      pointer-events: none;
    }
  }

  &--sort-asc svg {
    transform: scaleY(-1);
  }
}

.flex-table__filter {
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  z-index: 1;
  display: flex;
  align-items: center;
  height: 2em;
  pointer-events: none;
  margin: 0.2em;

  .filtered & {
    pointer-events: auto;
  }

  input {
    position: absolute;
    top: 0;
    left: 0;
    z-index: 1;
    width: 100%;
    border: 0;
    background: none;
    color: RGB(var(--anabar__text-RGB));
    background-color: #ebe5ff;
    opacity: 0;
    outline: 0;

    .active-filtered &:placeholder-shown,
    &:focus {
      z-index: 5;
      opacity: 1;
      border: none;
      background-color: #ffffff;
    }

    &:placeholder-shown + .flex-table__filter-inner {
      display: none;
    }

    &:hover:not(:focus),
    .flex-table__heads.filtered .flex-table__head-col:hover &:not(:focus) {
      opacity: 1;
      background-color: transparent;
      z-index: 5;
    }

    &:hover,
    &:focus {
      & + .flex-table__filter-inner {
        .flex-table__filter-text {
          opacity: 0 !important;
        }
      }
    }

    &::-webkit-input-placeholder {
      color: rgba(var(--anabar__text-RGB), 0.3);
    }
    &:-moz-placeholder {
      color: rgba(var(--anabar__text-RGB), 0.3);
    }
    &::-moz-placeholder {
      color: rgba(var(--anabar__text-RGB), 0.3);
    }
    &:-ms-input-placeholder {
      color: rgba(var(--anabar__text-RGB), 0.3);
    }
    &::-ms-input-placeholder {
      color: rgba(var(--anabar__text-RGB), 0.3);
    }
    &::placeholder {
      color: rgba(var(--anabar__text-RGB), 0.3);
    }
  }
}

.flex-table__filter input,
.flex-table__filter-inner {
  height: 100%;
  padding: 0.1em 0.2em 0.2em;
  line-height: 1.1;
  border-radius: 0.2em;
}

.flex-table__filter-inner {
  display: flex;
  align-items: center;
  overflow: hidden;
  white-space: nowrap;
  color: rgba(var(--anabar__text-RGB), 0.5);
  pointer-events: none;

  &:after {
    position: absolute;
    right: 0;
    top: 0;
    bottom: 0;
    z-index: 3;
    width: 2em;
    content: '';
    background-image: linear-gradient(
      to right,
      rgba(var(--anabar__surface-RGB), 0),
      rgba(var(--anabar__surface-RGB), 1) 0.8em
    );
  }
}

.flex-table__filter-text {
  position: relative;
  z-index: 2;
  padding-right: 0.2em;
  max-width: calc(100% - 1em);
}

.ids {
  code {
    background: transparent;
    padding: 0;
    font-family: 'Courier New', Courier, monospace;
    color: rgba(var(--anabar__text-RGB), 1);

    line-height: 1;
  }
}

.pagination {
  margin: 1em 0;
  display: flex;
  justify-content: flex-end;
  align-items: flex-start;

  &__arrow-left {
    transform: rotate(180deg);
  }
}

.pagination__button {
  display: block;
  color: currentColor !important;
  font-family: inherit !important;
  font-size: inherit !important;
  border: 0 !important;
  background-color: rgba(var(--anabar__surface-RGB), 1) !important;
  padding: 0.4em 0.6em 0.5em !important;
  border-bottom: 0 !important;
  border-radius: 0 !important;
  line-height: 1.15 !important;
  height: auto;

  &:hover {
    background-color: rgba(var(--anabar__hover-RGB), 0.1) !important;
    color: RGB(var(--anabar__hover-RGB)) !important;
  }

  &:focus {
    outline: none;
  }

  &:first-child {
    border-radius: 0.3em 0 0 0.3em !important;
  }
  &:last-child {
    border-radius: 0 0.3em 0.3em 0 !important;
  }
}

.pagination__input {
  width: 5em;
  height: auto !important;
  color: currentColor !important;
  font-family: inherit !important;
  font-size: inherit !important;
  background-color: transparent !important;
  border: 0.2em solid rgba(var(--anabar__surface-RGB), 1) !important;

  padding: 0.2em 0.6em 0.3em !important;

  transition: border-color 0.3s ease, color 0.3s ease;

  &:hover {
    background-color: transparent !important;
    transition: border-color 0s ease, color 0s ease !important;
    color: currentColor !important;
  }

  &:hover {
    border-color: rgba(var(--anabar__hover-RGB), 0.1) !important;
  }

  &:focus {
    box-shadow: none;
    border-color: rgba(var(--anabar__hover-RGB), 0.3) !important;
    color: rgba(var(--anabar__hover-RGB), 1) !important;
  }
}

.flex-table__empty-table-message {
  border-bottom: 1px solid rgba(var(--anabar__text-RGB), 0.1);
  border-left: 1px solid rgba(var(--anabar__text-RGB), 0.1);
  border-right: 1px solid rgba(var(--anabar__text-RGB), 0.1);
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 5em;
}

.flex_table__markdown {
  position: relative;
}
.flex_table__markdown .flex_table__markdown-tooltip {
  display: none;
}

.flex-table__body .td:hover {
  overflow: visible;
  text-overflow: ellipsis;
}

.flex_table__markdown:hover .flex_table__markdown-tooltip {
  position: absolute;
  display: block;
  top: 0;
  left: 0;
  z-index: 1000;
  background: white;
}

.flex_table__markdown:hover .flex_table__markdown-tooltip img {
  width: 15vw !important;
  height: auto !important;
}

@media screen and (max-device-width: 480px) {
  .mobile-view {
    .flex_table {
      &__info {
        flex-direction: column;
        height: auto;
        align-items: flex-start;
        padding: 7px 5px;
      }

      &__info-item {
        padding-left: 0;
        margin-bottom: 7px;
      }
    }

    &__bar {
      width: 100%;
    }

    &__settings {
      width: 100%;
    }
  }
}
